import BootPay from "bootpay-js";
import axios from "axios";

function pay(sellItemList, price, storeName, storeId, userId) {
    BootPay.request({
        // price: trInfo.Price,
        price: price,
        application_id: process.env.REACT_APP_BOOTPAY_APP_ID,
        // name: trInfo.storeName,
        name: storeName,
        pg: 'nicepay',
        // method: 'card',
        show_agree_window: 0,
        sellItemList: sellItemList,
        order_id: `${Date.now()}__`,
//        params: {callback1: '   1', callback2: '   2', customvar1234: ' '},
//        account_expire_at: '2018-05-25', //    ( yyyy-mm-dd  .  . )
    }).error(function (data) {
        console.log(data);
    }).cancel(function (data) {
        console.log(data);
    }).ready(function (data) {
        //      .
        console.log(data);
    }).confirm(function (data) {
        //   ,     .
        // -       .
        console.log(data);
        var enable = true; //       
        if (enable) {
            BootPay.transactionConfirm(data); //     .
        } else {
            BootPay.removePaymentWindow(); //         .
        }
    }).close(function (data) {
        //   . (,,   )
        // console.log(data);
    }).done(function (data) {
        //   
        //        .
        let transData = {"data":data, "sellItemList":sellItemList, 'storeId':storeId, 'userId':userId};
        // axios.post('http://localhost:8000/trade/', transData); URL EXCHANGE
        axios.post('/trade/', transData)
            .then((res)=>{
                window.location.href = res.data.url;
            });
    });
}

export default pay;

